:root {
  --text-colour-1: #0b090a;
  --text-colour-4: #aaa;
  --text-colour-2: #2b2b2b;
  --white: #fff;
}

.event-details {
  width: 100%;
  min-height: 100vh;
  position: relative;
  background: #f5f5f5;
  font-family:
    "Poppins",
    -apple-system,
    Roboto,
    Helvetica,
    sans-serif;
}

/* Browser Window Frame */
.browser-container {
  display: flex;
  width: 100%;
  height: 935px;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.browser-window {
  display: flex;
  width: 100%;
  height: 935px;
  justify-content: center;
  align-items: center;
  box-shadow:
    0px 0px 20.527px 0px rgba(0, 0, 0, 0.15),
    0px 25.659px 30.79px 0px rgba(0, 0, 0, 0.35);
}

.window-frame {
  width: 100%;
  height: 935px;
  border-radius: 10.263px;
  border: 0.513px solid rgba(0, 0, 0, 0.12);
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(41.05364990234375px);
}

/* Background Image */
.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.background-image img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Header */
.page-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1rem 0;
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brand-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-colour-1);
  cursor: pointer;
  margin: 0;
}

.main-navigation {
  display: flex;
  gap: 2rem;
  align-items: center;
}

.nav-item {
  background: none;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  color: var(--text-colour-2);
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.nav-item:hover {
  background: #f0f0f0;
}

.nav-item.active {
  background: #36506d;
  color: white;
}

.sign-out-btn {
  background: #36506d !important;
  color: white !important;
}

.user-profile {
  display: flex;
  align-items: center;
}

/* Main Content */
.main-content {
  position: relative;
  z-index: 100;
  max-width: 1200px;
  margin: 0 auto;
  padding: 8rem 2rem 4rem;
}

.report-header {
  margin-bottom: 3rem;
}

.report-title {
  font-size: 3rem;
  font-weight: 600;
  color: var(--text-colour-1);
  margin: 0 0 1rem 0;
  line-height: 1.2;
}

.report-publisher {
  font-size: 1.2rem;
  color: var(--text-colour-2);
  margin: 0;
}

.publisher-name {
  text-decoration: underline;
  font-weight: 500;
}

/* Report Details Card */
.report-details-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 3rem;
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.details-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.detail-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.detail-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background: #f0f8ff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.detail-content {
  flex: 1;
}

.detail-label {
  font-size: 1rem;
  font-weight: 600;
  color: #36506d;
  margin: 0 0 0.5rem 0;
}

.detail-value {
  font-size: 0.95rem;
  color: var(--text-colour-2);
  margin: 0;
  line-height: 1.4;
}

/* Content Sections */
.description-section,
.status-section,
.contact-section {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 2rem;
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.section-title {
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--text-colour-1);
  margin: 0 0 1.5rem 0;
}

.description-text {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-colour-2);
  margin: 0;
}

.status-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.status-item {
  font-size: 1rem;
  color: var(--text-colour-2);
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
}

.status-item::before {
  content: "•";
  color: #36506d;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.contact-item {
  font-size: 1rem;
  color: var(--text-colour-2);
  margin: 0;
}

.contact-item strong {
  color: var(--text-colour-1);
}

/* Footer */
.main-footer {
  position: relative;
  z-index: 100;
  margin-top: 4rem;
}

.footer-background {
  width: 100%;
  height: 221px;
  border: 1px solid #36506d;
  opacity: 0.1;
  background: linear-gradient(
    90deg,
    #8c8b89 39.42%,
    #f2f1ec 53.37%,
    #8c8b89 63.46%
  );
  position: absolute;
  top: 0;
}

.footer-container {
  position: relative;
  z-index: 10;
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 2rem 1rem;
}

.footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3rem;
  margin-bottom: 2rem;
}

.contact-location {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--text-colour-2);
  margin: 0 0 1rem 0;
}

.contact-details-footer {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-item-footer {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--text-colour-2);
}

.social-section {
  text-align: center;
}

.social-title {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--text-colour-1);
  margin: 0 0 1rem 0;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-link {
  background: none;
  border: none;
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: #f0f0f0;
}

.feedback-section {
  text-align: right;
}

.feedback-title {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--text-colour-1);
  margin: 0 0 0.5rem 0;
}

.feedback-subtitle {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--text-colour-1);
  margin: 0 0 1.5rem 0;
}

.feedback-form {
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: 415px;
}

.feedback-input {
  width: 100%;
  height: 71px;
  border-radius: 35.54px;
  border: 1px solid #36506d;
  background: #f5f5f5;
  box-shadow: 4.739px 11.847px 23.694px 0px rgba(0, 0, 0, 0.05);
  padding: 0 80px 0 24px;
  font-size: 18.955px;
  color: var(--text-colour-4);
  font-family: "Poppins", sans-serif;
  outline: none;
}

.feedback-input::placeholder {
  color: var(--text-colour-4);
}

.feedback-send-btn {
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  width: 59px;
  height: 59px;
  border-radius: 29.617px;
  background: #36506d;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.feedback-send-btn:hover {
  background: #2a3d54;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0;
  background: var(--text-colour-1);
  margin: 0 -2rem -1rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.copyright {
  color: var(--text-colour-4);
  font-size: 1rem;
}

.legal-links {
  display: flex;
  gap: 2.5rem;
}

.legal-link {
  background: none;
  border: none;
  color: var(--text-colour-4);
  font-size: 1rem;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
  transition: color 0.3s ease;
}

.legal-link:hover {
  color: white;
}

/* Responsive Design */
@media (max-width: 768px) {
  .header-container {
    padding: 0 1rem;
    flex-direction: column;
    gap: 1rem;
  }

  .main-navigation {
    flex-wrap: wrap;
    justify-content: center;
  }

  .main-content {
    padding: 10rem 1rem 2rem;
  }

  .report-title {
    font-size: 2rem;
  }

  .details-grid {
    grid-template-columns: 1fr;
  }

  .footer-content {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .feedback-section {
    text-align: center;
  }

  .footer-bottom {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .legal-links {
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .report-title {
    font-size: 1.5rem;
  }

  .nav-item {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
  }

  .feedback-form {
    max-width: 100%;
  }
}
